gsap.registerPlugin(ScrollTrigger);

function ScrollGorizontTwo(){

    const component = useRef();
    const slider = useRef();
  
    useLayoutEffect(() => {
      let ctx = gsap.context(() => {
        let panels = gsap.utils.toArray(".panel");
        gsap.to(panels, {
          xPercent: -100 * (panels.length - 1),
          ease: "none",
          scrollTrigger: {
            trigger: slider.current,
            pin: true,
            scrub: 1,
            snap: 1 / (panels.length - 1),
            end: () => "+=" + slider.current.offsetWidth
          }
        });
      }, component);
      return () => ctx.revert();
    });
   
    return(
        <section className="scrollGorizont__wrapper" style={{paddingTop:100}}>
            <div ref={component}>
                <div className="scrollGorizont__container__bottom" ref={slider}>
                    {[5, 6, 7].map(sectionNumber => (
                        <Section key={sectionNumber} sectionNumber={sectionNumber} />
                    ))}
                </div>
            </div>
        </section>
    )
}
